
import { Options, Vue } from 'vue-class-component'
import { Bar } from 'vue-chartjs'
import { Chart as ChartJS, Title, Tooltip, Legend, BarElement, CategoryScale, LinearScale } from 'chart.js'
import { chart01 } from '@/data'

ChartJS.register(Title, Tooltip, Legend, BarElement, CategoryScale, LinearScale)

const _chart = chart01()

@Options({

  mounted () {
    this.chartOptions = _chart.option
    this.chartDatas = _chart.data
  },

  components: {

    Bar

  },

  props: {

    _title: String

  },

  data () {
    return {
      chartOptions: {},
      chartDatas: {},
      chartClasses: 'chart-canvas',
      chartId: 'chart-bars',
      chartStyle: {
        height: '170px'
      }
    }
  }

})

export default class Card04 extends Vue {
  _title!: string;
  chartDatas!: Record<string, unknown>;
  chartOptions!: Record<string, unknown>;
  chartStyle!: Record<string, unknown>;
  chartClasses!: string;
  chartId!: string;
}

