

import { Options, Vue } from 'vue-class-component'

import RouterLayout from '@/components/router/_layout.vue' // @ is an alias to /src

import { getMenu } from '@/common/side_nav/'

import { userRoles } from '@/common/user'

@Options({

  data () {
    return {

      _path: '/about',

      _name: 'About'

    }
  },

  components: {

    RouterLayout

  },

  computed: {
    _display (): boolean {
      const _roles = userRoles()

      const _menu = getMenu()

      const _canDisplay = _menu.about.roles.indexOf(_roles) >= 0

      return _canDisplay
    }
  }

})

export default class AboutRouter extends Vue {
  _display!: boolean;

  _path!: string;

  _name!: string;
}

