

import { Options, Vue } from 'vue-class-component'

import { logout as logoutAction, toggleSideNav, navbarBlurOnScroll, fixedPlugin, search01OnFocus, search01OnFocusOut } from '@/common'

@Options({

  props: {

    msg: String,

    slotName: String

  },

  mounted () {
    navbarBlurOnScroll('navbarBlur')

    fixedPlugin()

    const iconNavbarSidenav = document.getElementById('iconNavbarSidenav')

    if (iconNavbarSidenav) {
      iconNavbarSidenav.addEventListener('click', toggleSideNav)
    }
  },

  methods: {

    logout (): void {
      logoutAction()
    },

    searchOnFocus (): void {
      search01OnFocus()
    },

    searchOnFocusOut (): void {
      search01OnFocusOut()
    }

  }

})

export default class ContentNav extends Vue {
  msg!: string;

  slotName!: string;

  logout!: any;

  searchOnFocus!: () => void;

  searchOnFocusOut!: () => void;
}

