
import { Options, Vue } from 'vue-class-component'
import { login as loginAction } from '@/common'

@Options({

  data () {
    return {

      uname: '',

      password: ''

    }
  },

  methods: {

    login (e: Event): boolean {
      e.preventDefault()
      const _params = {
        uname: this.uname,
        password: this.password
      }
      loginAction(_params)
      return false
    }

  },

  computed: {

    currentTimeFooter (): string {
      const _time = new Date().getFullYear()
      return _time.toString()
    }

  }

})

export default class SigninContent extends Vue {
  currentTimeFooter!: string;

  login!: (arg1: Event) => boolean;

  uname!: string;

  password!: string;
}
