import { API_BASE_URL, IS_PROD } from '@/config'

const API_URI_DEV = {
  login: 'login.php',
  auth: 'auth.php',
  data_01: 'data/data_01.php',
  test: 'test.php'
}

const uriProd = (uri:Record< string, string >): Record< string, string > => {
  for (const k in uri) {
    uri[k] = uri[k].substring(0, uri[k].lastIndexOf('.'))
  }
  return uri
}

const API_URI = IS_PROD ? uriProd(API_URI_DEV) : API_URI_DEV

const API_LIST = {
  login: API_BASE_URL + API_URI.login,
  auth: API_BASE_URL + API_URI.auth,
  data_01: API_BASE_URL + API_URI.data_01,
  test: API_BASE_URL + API_URI.test
}

export {
  API_LIST
}
